body {
  margin: 0;
  padding: 0;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  background-color: #1b1b1b;
}

.navbar {
  position: fixed;
  /* opacity: 0.5; */
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  flex-wrap: wrap;
  background-color: black;
  width: 100%;
  height: 70px;
  z-index: 1;
}

.nav {
  display: flex;
  justify-content: right;
  list-style: none;
  margin-right: 15%;
}

a {
  margin: 15px;
  color: #43dea5;
  text-decoration: none;
  text-transform: uppercase;
}

a:hover {
  color: white;
}

.banner-area {
  position: relative;
  width: 100%;
  height: 100vh;
  background-repeat: no-repeat;
  -webkit-background-size: cover;
  background-size: cover;
  background-color: white;
  /* background-image: url("http://localhost:3000/public/images/land.jpeg"); */
}

.homeImg{
  min-height: 100%;
  min-width: 100%;
  align-items: center;
  position: fixed;
}

.intro-area,
.ha-area,
.hd-area,
.sd-area,
.team-area,
.budget-area {
  position: relative;
  /* display: flex; */
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  flex-direction: row;
  width: 100%;
  height: 1000px;
  padding-top: 70px;
}
